@use "variables" as v;
@use "mixins" as m;

html {
  font-size: 55%;
  box-sizing: border-box;

  @include m.tablet {
    font-size: 50%;
  }
  @include m.phone {
    font-size: 46%;
  }
}
body {
  min-height: 100%;
  color: v.$white;
  font-family: v.$principalFont;
  // overflow-x: hidden;
  scroll-behavior: smooth;
}
*,
*:before,
*:after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
.container {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}
.container-sm {
  width: 85%;
  max-width: 1100px;
  margin: 0 auto;
}
.section {
  margin: 20rem auto;

  @include m.tablet {
    margin: 10rem auto;
  }
}
.bg-section {
  background-color: v.$bgSection;
  padding: 20rem 0;
}

a {
  text-decoration: none;
  color: v.$white;
}
ul,
li {
  list-style: none;
}

img {
  max-width: 100%;
  width: inherit;
  height: inherit;
}

#bg {
  position: fixed;
  z-index: -1;
  width: 100%;
  height: 100%;
  background: v.$bgColor;
}

.title {
  font-size: 4rem;
  text-align: center;
  width: 100%;
  margin-bottom: 3rem;
  @include m.tablet {
    text-align: center;
  }
}

.activeLink {
  color: v.$gradientColor1 !important;
}

.disclaimer {
  opacity: 0;
}

.separator {
  width: 100%;
  margin: 4rem auto;
  border: none;
  border-radius: 1rem;
  height: 0.3rem;
  outline: none;
  background: v.$gradientWebkit;
  background: v.$gradient;
}

.ocult {
  opacity: 0;
  overflow: hidden;
}
